<template >
  <div>
    <div class="login_log">
      <van-icon name="arrow-left" size="0.8rem" @click="rigth_go " />
      <p>手机号注册</p>
    </div>
    <div class="login_ipt">
      <van-field v-model="register.username" placeholder="请输入用户名" />
      <van-field v-model="register.mobile" placeholder="请输入手机号" />
      <van-field v-model="register.value" placeholder="请输入短信验证码">
        <van-button slot="button" size="small" type="primary" class="validation_bt">发送验证码</van-button>
      </van-field>
      <van-field v-model="register.password" type="password" placeholder="请输入登陆密码" />
      <van-field v-model="register.dbpwd" placeholder="请输入登陆密码" />
    </div>
    <div class="login_btn">
      <van-button @click="todoRegister" round type="primary" size="large" color="#e64d5b">注册</van-button>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data() {
    return {
      register: {}
    };
  },
  methods: {
    rigth_go() {
      this.$router.go(-1);
    },
    todoRegister() {
      console.log(this.register);
      this.$axios.post("/vue/register", this.register).then(res => {
        if (!!res.data.type) {
              this.$router.push({name:"loginPasswd"});
        } else {
          Toast('用户名格式错误')
        }
        this.register = {};
      });
    }
  }
};
</script>

<style   scoped>
.login_log {
  width: 100%;                                                                                    

                                                                                          
  height: 0.9rem;
  text-align: center;
  line-height: 0.9rem;
  position: relative;
  border-bottom: 1px solid #fff;
}

.van-icon-arrow-left {
  position: absolute;
  left: 0;
  top: 0;
  size: 0.8rem;
  width: 0.8rem;
  height: 0.8rem;
}
.van-field {
  background: #f5f4f0;
  height: 1.08rem;
  border-bottom: 1px solid #fff;
}
.login_ipt {
  width: 100%;
  height: 5rem;
  margin: 1rem 0;
}
.validation_bt {
  color: #111;
  background: #f5f4f0;
  border: none;
}
.login_btn {
  width: 94%;
  margin: 0 auto;
}
</style>